<template>
  <Layout class="professional-detail">
    <Row class="tool-bar" type="flex">
      <a href="javascript: void(0)" @click="$router.go(-1)">
        <icon type="ios-arrow-back" />
        返回
      </a>

      <div class="tool-right">
        <Button v-if="pageType === 'edit'" type="error" size="small" @click.native="deleteProfessional">删除</Button>
      </div>
    </Row>

    <Form
      :model="formData"
      ref="formRefs"
      :rules="validators"
      class="professional-form"
      :label-width="72">
      <FormItem label="上级条目" prop="parentId">
        <professional-selector :professionalId="formData.parentId" @on-select="selectParent"/>
      </FormItem>
      <FormItem label="专业名称" prop="name">
        <Input v-model="formData.name"/>
      </FormItem>
      <FormItem label="专业别名">
        <Input v-model="formData.alias"/>
      </FormItem>
      <FormItem label="检索索引">
        <Input v-model="formData.indexKey"/>
      </FormItem>
      <FormItem label="显示顺序">
        <Input v-model="formData.sort"/>
      </FormItem>
    </Form>

    <Row class="bottom-btns">
      <Button type="primary" size="small" @click.native="saveProfessional">保存</Button>
    </Row>
  </Layout>
</template>

<script>
import { saveProfessional, deleteProfessional } from '@/api/professional'
import professionalSelector from '@/components/professional-selector'

export default {
  name: 'professional-detail',
  components: {
    'professional-selector': professionalSelector
  },
  data () {
    const validateParent = (rule, value, callback) => {
      if ('' + value && ('' + value === this.formData.id)) {
        callback(new Error('不能选择自己为上级'))
      } else {
        callback()
      }
    }

    return {
      pageType: '',
      formData: {
        id: null,
        name: '',
        parentId: null,
        alias: '',
        sort: 0,
        indexKey: '',
        level: 0
      },
      validators: {
        parentId: [
          { validator: validateParent, trigger: 'change' }
        ],
        name: [
          { required: true, message: '请输入专业分类名称', trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      this.pageType = this.$route.params.type
      if (this.pageType === 'edit') {
        this.formData = JSON.parse(this.$route.params.data)
      }
    },
    selectParent (data) {
      if (data) {
        this.formData.parentId = data.id
        this.formData.level = +data.level + 1
      }
    },
    saveProfessional () {
      this.$refs.formRefs.validate(valid => {
        if (valid) {
          saveProfessional(this.formData).then(resp => {
            if (resp.response.success) {
              this.$Message.success('保存成功！')
              this.$router.go(-1)
            } else {
              this.$Message.error(resp.response.msg)
            }
          }).catch(err => {
            console.error(err)
          })
        }
      })
    },
    deleteProfessional () {
      deleteProfessional({ ids: [this.formData.id] }).then(resp => {
        if (resp.response.success) {
          this.$Message.success('删除成功！')
          this.$router.go(-1)
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    }
  }
}
</script>

<style scoped lang="stylus">
.professional-detail
  padding 0.5rem
  height 100%
  background none
  max-width 720px

  .tool-bar
    margin-bottom 0.5rem
    .tool-right
      flex 1 1 auto
      text-align right
  .professional-form
  .bottom-btns
    text-align right
</style>
